h4 {
  background-color: #006400;
  text-align: center;
  color: #ffffff;
  padding: 10px; }

table {
  border: 2px solid #ECF0F1; }

i {
  margin-right: 10px;
  margin-left: 10px; }

td, th {
  text-align: left; }

td {
  white-space: nowrap; }

img {
  width: 30px;
  height: 30px;
  border-radius: 40%;
  border: 1px solid #817a79;
  margin-right: 25px; }

#recent, #alltime {
  cursor: pointer; }

.fa-angle-double-down {
  color: #006400;
  font-weight: bold; }

footer {
  height: 40px;
  text-align: center;
  background-color: #006400;
  color: #ffffff;
  line-height: 40px; }
  footer a {
    color: #ffffff; }

@media (max-width: 600px) {
  img {
    width: 18px;
    height: 18px;
    margin-right: 5px;
    padding-left: 0; }
  table {
    font-size: 10px; } }



